/**
 * Webhook Recovery System
 *
 * Stores failed webhook events in Firestore for manual retry and debugging.
 * Provides recovery mechanisms for Twilio and Lob webhook failures.
 *
 * Features:
 * - Store failed webhook payloads with full context
 * - Manual retry via admin UI
 * - Automatic expiration after 30 days
 * - Correlation IDs for debugging
 */

import { db, Timestamp } from '@/lib/firebase';
import { logError, logInfo } from '@/utils/logger';

export interface FailedWebhookEvent {
  id?: string;
  source: 'twilio' | 'lob';
  eventType: string;
  payload: any;
  headers: Record<string, string>;
  url: string;
  error: {
    message: string;
    stack?: string;
    code?: string;
  };
  correlationId: string;
  attemptCount: number;
  status: 'failed' | 'retrying' | 'recovered' | 'abandoned';
  createdAt: Timestamp;
  updatedAt: Timestamp;
  expiresAt: Timestamp; // Auto-delete after 30 days
  retryHistory: Array<{
    attemptedAt: Timestamp;
    error?: string;
    success: boolean;
  }>;
}

/**
 * Store a failed webhook event for later recovery
 */
export async function storeFailedWebhook(params: {
  source: 'twilio' | 'lob';
  eventType: string;
  payload: any;
  headers: Record<string, string>;
  url: string;
  error: Error;
  correlationId: string;
}): Promise<string> {
  try {
    const now = Timestamp.now();
    const expiresAt = Timestamp.fromMillis(now.toMillis() + 30 * 24 * 60 * 60 * 1000); // 30 days

    const failedEvent: Omit<FailedWebhookEvent, 'id'> = {
      source: params.source,
      eventType: params.eventType,
      payload: params.payload,
      headers: params.headers,
      url: params.url,
      error: {
        message: params.error.message,
        stack: params.error.stack,
        code: (params.error as any).code,
      },
      correlationId: params.correlationId,
      attemptCount: 1,
      status: 'failed',
      createdAt: now,
      updatedAt: now,
      expiresAt,
      retryHistory: [
        {
          attemptedAt: now,
          error: params.error.message,
          success: false,
        },
      ],
    };

    const docRef = await db.collection('failed_webhooks').add(failedEvent);

    logInfo('Failed webhook stored for recovery', {
      id: docRef.id,
      source: params.source,
      eventType: params.eventType,
      correlationId: params.correlationId,
    });

    return docRef.id;
  } catch (error: any) {
    logError('Failed to store webhook event', error);
    throw error;
  }
}

/**
 * Retry a failed webhook event
 */
export async function retryFailedWebhook(
  eventId: string,
  processor: (payload: any, headers: Record<string, string>) => Promise<void>
): Promise<boolean> {
  try {
    const eventDoc = await db.collection('failed_webhooks').doc(eventId).get();

    if (!eventDoc.exists) {
      throw new Error(`Failed webhook event ${eventId} not found`);
    }

    const event = eventDoc.data() as FailedWebhookEvent;

    if (event.status === 'recovered') {
      logInfo('Webhook already recovered', { eventId });
      return true;
    }

    if (event.status === 'abandoned') {
      logInfo('Webhook was abandoned, cannot retry', { eventId });
      return false;
    }

    // Update status to retrying
    await eventDoc.ref.update({
      status: 'retrying',
      updatedAt: Timestamp.now(),
    });

    logInfo('Retrying failed webhook', {
      eventId,
      source: event.source,
      eventType: event.eventType,
      attemptCount: event.attemptCount + 1,
    });

    try {
      // Execute the webhook processor
      await processor(event.payload, event.headers);

      // Mark as recovered
      await eventDoc.ref.update({
        status: 'recovered',
        attemptCount: event.attemptCount + 1,
        updatedAt: Timestamp.now(),
        retryHistory: [
          ...event.retryHistory,
          {
            attemptedAt: Timestamp.now(),
            success: true,
          },
        ],
      });

      logInfo('Webhook recovered successfully', { eventId });
      return true;
    } catch (retryError: any) {
      // Mark retry attempt
      await eventDoc.ref.update({
        status: event.attemptCount >= 5 ? 'abandoned' : 'failed',
        attemptCount: event.attemptCount + 1,
        updatedAt: Timestamp.now(),
        retryHistory: [
          ...event.retryHistory,
          {
            attemptedAt: Timestamp.now(),
            error: retryError.message,
            success: false,
          },
        ],
      });

      logError('Webhook retry failed', retryError, {
        eventId,
        attemptCount: event.attemptCount + 1,
      });

      return false;
    }
  } catch (error: any) {
    logError('Error retrying failed webhook', error, { eventId });
    return false;
  }
}

/**
 * Get all failed webhooks for admin UI
 */
export async function getFailedWebhooks(params?: {
  source?: 'twilio' | 'lob';
  status?: 'failed' | 'retrying' | 'recovered' | 'abandoned';
  limit?: number;
}): Promise<FailedWebhookEvent[]> {
  try {
    let query = db.collection('failed_webhooks').orderBy('createdAt', 'desc');

    if (params?.source) {
      query = query.where('source', '==', params.source) as any;
    }

    if (params?.status) {
      query = query.where('status', '==', params.status) as any;
    }

    const limit = params?.limit || 50;
    const snapshot = await query.limit(limit).get();

    return snapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    })) as FailedWebhookEvent[];
  } catch (error: any) {
    logError('Error fetching failed webhooks', error);
    return [];
  }
}

/**
 * Delete old failed webhooks (called by cron job)
 */
export async function cleanupExpiredWebhooks(): Promise<number> {
  try {
    const now = Timestamp.now();
    const expiredSnapshot = await db
      .collection('failed_webhooks')
      .where('expiresAt', '<=', now)
      .limit(100)
      .get();

    if (expiredSnapshot.empty) {
      return 0;
    }

    const batch = db.batch();
    expiredSnapshot.docs.forEach((doc) => {
      batch.delete(doc.ref);
    });

    await batch.commit();

    logInfo('Cleaned up expired failed webhooks', {
      count: expiredSnapshot.size,
    });

    return expiredSnapshot.size;
  } catch (error: any) {
    logError('Error cleaning up expired webhooks', error);
    return 0;
  }
}

/**
 * Retry all eligible failed webhooks
 * Called by cron job every 30 minutes
 *
 * Retry Strategy:
 * - Exponential backoff: 1m, 5m, 15m, 1h, 6h
 * - Max 5 attempts per webhook
 * - Only retries webhooks with nextRetryAt <= now
 *
 * @returns Statistics about retry attempts
 */
export async function retryFailedWebhooks(): Promise<{
  retriedCount: number;
  successCount: number;
  failedCount: number;
}> {
  try {
    const now = Timestamp.now();

    // Get all failed webhooks that are eligible for retry
    const eligibleSnapshot = await db
      .collection('failed_webhooks')
      .where('status', 'in', ['failed', 'retrying'])
      .where('attemptCount', '<', 5)
      .limit(100)
      .get();

    if (eligibleSnapshot.empty) {
      logInfo('No failed webhooks to retry');
      return { retriedCount: 0, successCount: 0, failedCount: 0 };
    }

    let successCount = 0;
    let failedCount = 0;

    // Process each failed webhook with exponential backoff
    for (const doc of eligibleSnapshot.docs) {
      const event = doc.data() as FailedWebhookEvent;

      // Calculate next retry time using exponential backoff
      const backoffMinutes = getBackoffMinutes(event.attemptCount);
      const nextRetryAt = event.updatedAt.toMillis() + backoffMinutes * 60 * 1000;

      // Skip if not ready for retry yet
      if (now.toMillis() < nextRetryAt) {
        continue;
      }

      // Determine processor based on source
      const processor = await getWebhookProcessor(event.source);
      if (!processor) {
        logError('No processor found for webhook source', new Error('Unknown source'), {
          source: event.source,
          eventId: doc.id,
        });
        failedCount++;
        continue;
      }

      // Retry the webhook
      const success = await retryFailedWebhook(doc.id, processor);
      if (success) {
        successCount++;
      } else {
        failedCount++;
      }
    }

    logInfo('Webhook retry batch complete', {
      retriedCount: successCount + failedCount,
      successCount,
      failedCount,
    });

    return {
      retriedCount: successCount + failedCount,
      successCount,
      failedCount,
    };
  } catch (error: any) {
    logError('Error retrying failed webhooks', error);
    return { retriedCount: 0, successCount: 0, failedCount: 0 };
  }
}

/**
 * Get exponential backoff time in minutes
 * Attempt 1: 1 minute
 * Attempt 2: 5 minutes
 * Attempt 3: 15 minutes
 * Attempt 4: 60 minutes (1 hour)
 * Attempt 5: 360 minutes (6 hours)
 */
function getBackoffMinutes(attemptCount: number): number {
  const backoffSchedule = [1, 5, 15, 60, 360];
  return backoffSchedule[Math.min(attemptCount, backoffSchedule.length - 1)];
}

/**
 * Get webhook processor function based on source
 * Returns a function that processes the webhook payload
 */
async function getWebhookProcessor(
  source: 'twilio' | 'lob'
): Promise<((payload: any, headers: Record<string, string>) => Promise<void>) | null> {
  // Dynamically import webhook handlers to avoid circular dependencies

  if (source === 'twilio') {
    const { processTwilioWebhook } = await import('@/lib/webhook-processors/twilio');
    return processTwilioWebhook;
  }

  if (source === 'lob') {
    const { processLobWebhook } = await import('@/lib/webhook-processors/lob');
    return processLobWebhook;
  }

  return null;
}

/**
 * Generate correlation ID for request tracking
 */
export function generateCorrelationId(): string {
  return `${Date.now()}-${Math.random().toString(36).substring(2, 15)}`;
}
