/**
 * CSRF Protection Middleware
 *
 * Protects against Cross-Site Request Forgery attacks by:
 * 1. Validating Origin/Referer headers
 * 2. Checking CSRF tokens for state-changing operations
 * 3. Enforcing same-origin policy for sensitive endpoints
 *
 * Documentation:
 * https://owasp.org/www-community/attacks/csrf
 */

import { NextRequest } from 'next/server';
import crypto from 'crypto';
import { logWarn, logError } from '@/utils/logger';

/**
 * Validate Origin header for webhook endpoints
 *
 * Webhooks from Twilio/Lob should not have Origin headers (server-to-server).
 * If an Origin header is present, it indicates a browser request (potential CSRF).
 */
export function validateWebhookOrigin(req: NextRequest): boolean {
  const origin = req.headers.get('origin');
  const referer = req.headers.get('referer');

  // Webhooks from external services should NOT have Origin headers
  // If Origin is present, it's likely a browser-based CSRF attack
  if (origin) {
    logWarn('Webhook received with Origin header - potential CSRF attack', {
      origin,
      url: req.url,
    });
    return false;
  }

  // Referer is also unusual for server-to-server webhooks
  if (referer) {
    logWarn('Webhook received with Referer header - potential CSRF attack', {
      referer,
      url: req.url,
    });
    return false;
  }

  return true;
}

/**
 * Validate Origin header for admin API endpoints
 *
 * Admin endpoints should only accept requests from the same origin.
 */
export function validateAdminOrigin(req: NextRequest): boolean {
  const origin = req.headers.get('origin');
  const appUrl = process.env.NEXT_PUBLIC_APP_URL;

  if (!appUrl) {
    logError('NEXT_PUBLIC_APP_URL not configured - cannot validate origin');
    return false;
  }

  // For GET requests, Origin may be null (direct navigation)
  if (req.method === 'GET') {
    return true;
  }

  // For POST/PUT/DELETE, require matching origin
  if (!origin) {
    logWarn('Missing Origin header on admin request', { url: req.url });
    return false;
  }

  const allowedOrigins = [
    appUrl,
    appUrl.replace('https://', 'http://'), // Allow http in development
  ];

  if (!allowedOrigins.includes(origin)) {
    logWarn('Invalid origin for admin request', {
      origin,
      expected: appUrl,
      url: req.url,
    });
    return false;
  }

  return true;
}

/**
 * Generate CSRF token
 *
 * Used for form submissions and admin actions.
 */
export function generateCSRFToken(): string {
  return crypto.randomBytes(32).toString('hex');
}

/**
 * Verify CSRF token
 *
 * Compares submitted token with expected token.
 */
export function verifyCSRFToken(submittedToken: string, expectedToken: string): boolean {
  if (!submittedToken || !expectedToken) {
    return false;
  }

  try {
    // Use timing-safe comparison to prevent timing attacks
    return crypto.timingSafeEqual(
      Buffer.from(submittedToken),
      Buffer.from(expectedToken)
    );
  } catch (error) {
    // Tokens are different lengths
    return false;
  }
}

/**
 * Extract CSRF token from request
 *
 * Checks:
 * 1. X-CSRF-Token header
 * 2. csrf_token form field
 * 3. _csrf form field (common convention)
 */
export async function extractCSRFToken(req: NextRequest): Promise<string | null> {
  // Check header first
  const headerToken = req.headers.get('x-csrf-token');
  if (headerToken) {
    return headerToken;
  }

  // Check form data
  try {
    const contentType = req.headers.get('content-type');

    if (contentType?.includes('application/json')) {
      const body = await req.json();
      return body.csrf_token || body._csrf || null;
    }

    if (contentType?.includes('application/x-www-form-urlencoded')) {
      const formData = await req.formData();
      return (formData.get('csrf_token') as string) || (formData.get('_csrf') as string) || null;
    }
  } catch (error) {
    // Body already consumed or invalid
    return null;
  }

  return null;
}

/**
 * Validate Content-Type for POST requests
 *
 * Ensures requests are sent with correct Content-Type to prevent
 * simple form-based CSRF attacks.
 */
export function validateContentType(req: NextRequest, expectedTypes: string[]): boolean {
  const contentType = req.headers.get('content-type');

  if (!contentType) {
    logWarn('Missing Content-Type header', { url: req.url });
    return false;
  }

  const isValid = expectedTypes.some((type) => contentType.includes(type));

  if (!isValid) {
    logWarn('Invalid Content-Type', {
      contentType,
      expected: expectedTypes,
      url: req.url,
    });
  }

  return isValid;
}

/**
 * Check if request is from same site
 *
 * Uses SameSite cookie attribute and origin validation.
 */
export function isSameSiteRequest(req: NextRequest): boolean {
  const origin = req.headers.get('origin');
  const referer = req.headers.get('referer');
  const host = req.headers.get('host');

  // If no origin or referer, assume same-site (direct navigation)
  if (!origin && !referer) {
    return true;
  }

  // Extract hostname from origin or referer
  let sourceHost: string | null = null;
  try {
    if (origin) {
      sourceHost = new URL(origin).host;
    } else if (referer) {
      sourceHost = new URL(referer).host;
    }
  } catch (error) {
    logWarn('Invalid origin or referer URL', { origin, referer });
    return false;
  }

  // Check if source matches request host
  return sourceHost === host;
}
