/**
 * Twilio Webhook Signature Verification
 *
 * Verifies that incoming webhooks are actually from Twilio
 * using HMAC SHA-256 signature validation.
 *
 * Documentation:
 * https://www.twilio.com/docs/usage/security#validating-requests
 */

import { NextRequest } from 'next/server';
import crypto from 'crypto';
import { logWarn, logError } from '@/utils/logger';

/**
 * Verify Twilio webhook signature
 * @param req - Next.js request object
 * @returns true if signature is valid
 */
export async function verifyTwilioSignature(req: NextRequest): Promise<boolean> {
  try {
    const authToken = process.env.TWILIO_AUTH_TOKEN;

    if (!authToken) {
      logError('TWILIO_AUTH_TOKEN not configured - cannot verify webhook signature', new Error('Missing auth token'));
      // SECURITY: Never bypass verification, even in development
      // Use ngrok + real Twilio webhooks for local testing
      return false;
    }

    // Get signature from header
    const signature = req.headers.get('x-twilio-signature');
    if (!signature) {
      logWarn('Missing x-twilio-signature header');
      return false;
    }

    // Get full URL
    const url = req.url;

    // Get POST parameters
    const formData = await req.formData();
    const params: Record<string, string> = {};
    formData.forEach((value, key) => {
      params[key] = value.toString();
    });

    // Compute expected signature
    const expectedSignature = computeTwilioSignature(url, params, authToken);

    // Compare signatures
    return crypto.timingSafeEqual(
      Buffer.from(signature),
      Buffer.from(expectedSignature)
    );
  } catch (error: any) {
    logWarn('Error verifying Twilio signature', error);
    return false;
  }
}

/**
 * Compute Twilio signature for comparison
 * @param url - Full webhook URL
 * @param params - POST parameters
 * @param authToken - Twilio auth token
 * @returns HMAC SHA-256 signature in base64
 */
function computeTwilioSignature(
  url: string,
  params: Record<string, string>,
  authToken: string
): string {
  // Sort parameters alphabetically and concatenate
  const sortedKeys = Object.keys(params).sort();
  let data = url;

  for (const key of sortedKeys) {
    data += key + params[key];
  }

  // Compute HMAC SHA-256
  const hmac = crypto.createHmac('sha256', authToken);
  hmac.update(data);
  return hmac.digest('base64');
}

/**
 * Extract form data from request without consuming it
 * @param req - Next.js request object
 * @returns Form data as object
 */
export async function getTwilioFormData(
  req: NextRequest
): Promise<Record<string, string>> {
  const formData = await req.formData();
  const params: Record<string, string> = {};

  formData.forEach((value, key) => {
    params[key] = value.toString();
  });

  return params;
}
