/**
 * Webhook Rate Limiting
 *
 * Protects webhook endpoints from DoS attacks and abuse by:
 * 1. Rate limiting per IP address
 * 2. Rate limiting per webhook source (Twilio/Lob)
 * 3. Sliding window algorithm for accurate rate limiting
 *
 * Uses Upstash Redis for distributed rate limiting across serverless functions.
 */

import { NextRequest } from 'next/server';
import { ratelimit } from '@/lib/ratelimit';
import { logWarn } from '@/utils/logger';

/**
 * Rate limit configuration for webhooks
 *
 * More permissive than API endpoints since legitimate webhook
 * traffic can be bursty (e.g., batch SMS status updates).
 */
const WEBHOOK_RATE_LIMITS = {
  // Per IP address: 100 requests per minute
  perIP: {
    requests: 100,
    window: '1 m',
  },
  // Per source (Twilio/Lob): 500 requests per minute
  perSource: {
    requests: 500,
    window: '1 m',
  },
  // Per specific webhook (e.g., call-status): 200 requests per minute
  perWebhook: {
    requests: 200,
    window: '1 m',
  },
};

/**
 * Check rate limit for webhook request
 *
 * Returns { allowed: true } if request is within limits
 * Returns { allowed: false, ... } if rate limit exceeded
 */
export async function checkWebhookRateLimit(params: {
  req: NextRequest;
  source: 'twilio' | 'lob';
  webhookType: string;
}): Promise<{
  allowed: boolean;
  limit?: number;
  remaining?: number;
  reset?: number;
  retryAfter?: number;
}> {
  try {
    // Get client IP
    const ip = getClientIP(params.req);
    if (!ip) {
      // If we can't determine IP, allow request but log warning
      logWarn('Unable to determine client IP for rate limiting');
      return { allowed: true };
    }

    // Check IP-based rate limit
    const ipKey = `webhook:ratelimit:ip:${ip}`;
    const ipLimit = await ratelimit.limit(ipKey);

    if (!ipLimit.success) {
      logWarn('Webhook rate limit exceeded for IP', {
        ip,
        source: params.source,
        webhookType: params.webhookType,
      });

      return {
        allowed: false,
        limit: ipLimit.limit,
        remaining: ipLimit.remaining,
        reset: ipLimit.reset,
        retryAfter: Math.ceil((ipLimit.reset - Date.now()) / 1000),
      };
    }

    // Check source-based rate limit
    const sourceKey = `webhook:ratelimit:source:${params.source}`;
    const sourceLimit = await ratelimit.limit(sourceKey);

    if (!sourceLimit.success) {
      logWarn('Webhook rate limit exceeded for source', {
        ip,
        source: params.source,
        webhookType: params.webhookType,
      });

      return {
        allowed: false,
        limit: sourceLimit.limit,
        remaining: sourceLimit.remaining,
        reset: sourceLimit.reset,
        retryAfter: Math.ceil((sourceLimit.reset - Date.now()) / 1000),
      };
    }

    // Check webhook-specific rate limit
    const webhookKey = `webhook:ratelimit:webhook:${params.source}:${params.webhookType}`;
    const webhookLimit = await ratelimit.limit(webhookKey);

    if (!webhookLimit.success) {
      logWarn('Webhook rate limit exceeded for webhook type', {
        ip,
        source: params.source,
        webhookType: params.webhookType,
      });

      return {
        allowed: false,
        limit: webhookLimit.limit,
        remaining: webhookLimit.remaining,
        reset: webhookLimit.reset,
        retryAfter: Math.ceil((webhookLimit.reset - Date.now()) / 1000),
      };
    }

    // All rate limits passed
    return {
      allowed: true,
      limit: webhookLimit.limit,
      remaining: webhookLimit.remaining,
      reset: webhookLimit.reset,
    };
  } catch (error: any) {
    // If rate limiting fails (Redis down, etc.), allow the request
    // but log the error
    logWarn('Webhook rate limiting failed - allowing request', error);
    return { allowed: true };
  }
}

/**
 * Extract client IP from request
 *
 * Checks multiple headers in order of preference:
 * 1. x-forwarded-for (Vercel/proxy)
 * 2. x-real-ip (nginx)
 * 3. cf-connecting-ip (Cloudflare)
 * 4. req.ip (direct connection)
 */
function getClientIP(req: NextRequest): string | null {
  // Check x-forwarded-for (most common)
  const forwardedFor = req.headers.get('x-forwarded-for');
  if (forwardedFor) {
    // Take first IP if multiple
    return forwardedFor.split(',')[0].trim();
  }

  // Check x-real-ip
  const realIP = req.headers.get('x-real-ip');
  if (realIP) {
    return realIP.trim();
  }

  // Check Cloudflare
  const cfIP = req.headers.get('cf-connecting-ip');
  if (cfIP) {
    return cfIP.trim();
  }

  // Check Vercel geo
  const vercelIP = req.headers.get('x-vercel-forwarded-for');
  if (vercelIP) {
    return vercelIP.trim();
  }

  // Fallback - try to extract from URL or connection
  // Note: In serverless environments, this might not be available
  return null;
}

/**
 * Get rate limit headers for response
 *
 * Adds standard rate limit headers to response:
 * - X-RateLimit-Limit
 * - X-RateLimit-Remaining
 * - X-RateLimit-Reset
 * - Retry-After (if rate limited)
 */
export function getRateLimitHeaders(rateLimitResult: {
  allowed: boolean;
  limit?: number;
  remaining?: number;
  reset?: number;
  retryAfter?: number;
}): Record<string, string> {
  const headers: Record<string, string> = {};

  if (rateLimitResult.limit !== undefined) {
    headers['X-RateLimit-Limit'] = rateLimitResult.limit.toString();
  }

  if (rateLimitResult.remaining !== undefined) {
    headers['X-RateLimit-Remaining'] = rateLimitResult.remaining.toString();
  }

  if (rateLimitResult.reset !== undefined) {
    headers['X-RateLimit-Reset'] = rateLimitResult.reset.toString();
  }

  if (!rateLimitResult.allowed && rateLimitResult.retryAfter !== undefined) {
    headers['Retry-After'] = rateLimitResult.retryAfter.toString();
  }

  return headers;
}
