/**
 * Twilio SMS Reply Webhook
 * POST /api/webhooks/twilio/sms-reply
 *
 * Receives incoming SMS replies from recipients:
 * - STOP: Opt-out from SMS (PECR compliance)
 * - START: Opt back in (optional)
 * - HELP: Send help information
 * - Any other reply: Log for manual review
 *
 * PECR Requirement: Must honor STOP requests immediately
 */

import { NextRequest, NextResponse } from 'next/server';
import { verifyTwilioSignature } from '@/lib/twilio-verify';
import { handleSMSOptOut } from '@/lib/twilio-sms';
import { logInfo, logError } from '@/utils/logger';
import { validateWebhookOrigin, validateContentType } from '@/lib/csrf-protection';
import { checkWebhookRateLimit, getRateLimitHeaders } from '@/lib/webhook-ratelimit';
import { generateCorrelationId } from '@/lib/webhook-recovery';
import { db, Timestamp } from '@/lib/firebase';

export const dynamic = 'force-dynamic';

export async function POST(req: NextRequest): Promise<NextResponse> {
  const correlationId = generateCorrelationId();

  try {
    // 1. Rate limiting
    const rateLimit = await checkWebhookRateLimit({
      req,
      source: 'twilio',
      webhookType: 'sms-reply',
    });

    if (!rateLimit.allowed) {
      logError('Rate limit exceeded for Twilio SMS reply webhook', undefined, { correlationId });
      return NextResponse.json(
        { error: 'Too many requests' },
        {
          status: 429,
          headers: getRateLimitHeaders(rateLimit),
        }
      );
    }

    // 2. CSRF Protection
    if (!validateWebhookOrigin(req)) {
      logError('CSRF: Invalid origin on webhook', undefined, { correlationId });
      return NextResponse.json({ error: 'Forbidden' }, { status: 403 });
    }

    // 3. Validate Content-Type
    if (!validateContentType(req, ['application/x-www-form-urlencoded'])) {
      logError('CSRF: Invalid Content-Type', undefined, { correlationId });
      return NextResponse.json({ error: 'Invalid Content-Type' }, { status: 400 });
    }

    // 4. Verify Twilio signature
    const isValid = await verifyTwilioSignature(req);
    if (!isValid) {
      logError('Invalid Twilio signature on SMS reply webhook', undefined, { correlationId });
      return NextResponse.json({ error: 'Invalid signature' }, { status: 401 });
    }

    // 5. Parse form data
    const formData = await req.formData();
    const from = formData.get('From') as string; // Sender's phone number
    const body = (formData.get('Body') as string)?.trim().toUpperCase() || '';
    const messageSid = formData.get('MessageSid') as string;

    logInfo('Received SMS reply', {
      from,
      body: body.substring(0, 50), // Log first 50 chars only
      messageSid,
      correlationId,
    });

    // 6. Handle opt-out keywords (PECR compliance)
    const optOutKeywords = ['STOP', 'STOPALL', 'UNSUBSCRIBE', 'CANCEL', 'END', 'QUIT'];
    if (optOutKeywords.includes(body)) {
      const result = await handleSMSOptOut(from);

      if (result.success) {
        logInfo('SMS opt-out processed', {
          from,
          usersUpdated: result.usersUpdated,
          correlationId,
        });

        // Respond with confirmation (Twilio will send this to user)
        return new NextResponse(
          `<?xml version="1.0" encoding="UTF-8"?>
<Response>
  <Message>You have been unsubscribed from SMS notifications. You will not receive any more messages from us. Reply START to opt back in.</Message>
</Response>`,
          {
            headers: {
              'Content-Type': 'text/xml',
            },
          }
        );
      } else {
        logError('Failed to process SMS opt-out', undefined, {
          from,
          error: result.error,
          correlationId,
        });
      }
    }

    // 7. Handle opt-in keywords
    const optInKeywords = ['START', 'YES', 'UNSTOP'];
    if (optInKeywords.includes(body)) {
      // Re-enable SMS for this phone number
      const usersSnapshot = await db
        .collection('users')
        .where('phoneNumber', '==', from)
        .get();

      if (!usersSnapshot.empty) {
        const batch = db.batch();
        const now = Timestamp.now();

        usersSnapshot.docs.forEach((doc) => {
          batch.update(doc.ref, {
            'collectionsConsent.smsConsent': true,
            'collectionsConsent.smsOptedOut': false,
            'collectionsConsent.smsOptOutDate': null,
            updatedAt: now,
          });
        });

        await batch.commit();

        logInfo('SMS opt-in processed', {
          from,
          usersUpdated: usersSnapshot.size,
          correlationId,
        });

        return new NextResponse(
          `<?xml version="1.0" encoding="UTF-8"?>
<Response>
  <Message>You have been resubscribed to SMS notifications. Reply STOP to opt out at any time.</Message>
</Response>`,
          {
            headers: {
              'Content-Type': 'text/xml',
            },
          }
        );
      }
    }

    // 8. Handle HELP keyword
    if (body === 'HELP' || body === 'INFO') {
      return new NextResponse(
        `<?xml version="1.0" encoding="UTF-8"?>
<Response>
  <Message>Relay Collections: Automated payment reminders. Reply STOP to unsubscribe or START to resubscribe. For support, visit relay.com/support</Message>
</Response>`,
        {
          headers: {
            'Content-Type': 'text/xml',
          },
        }
      );
    }

    // 9. Log other replies for manual review
    logInfo('SMS reply received (not a keyword)', {
      from,
      body: body.substring(0, 100),
      correlationId,
    });

    // Store reply in database for admin review
    await db.collection('sms_replies').add({
      from,
      body,
      messageSid,
      receivedAt: Timestamp.now(),
      correlationId,
      reviewed: false,
    });

    // No response needed for non-keyword replies
    return new NextResponse(
      `<?xml version="1.0" encoding="UTF-8"?>
<Response></Response>`,
      {
        headers: {
          'Content-Type': 'text/xml',
        },
      }
    );
  } catch (error: any) {
    logError('SMS reply webhook error', error, { correlationId });
    // Return 200 to prevent Twilio retries
    return new NextResponse(
      `<?xml version="1.0" encoding="UTF-8"?>
<Response></Response>`,
      {
        headers: {
          'Content-Type': 'text/xml',
        },
        status: 200,
      }
    );
  }
}
